import { Controller } from '@ali/4ever-cangjie';
export default function isTaskList(editor: Controller) {
  const { value } = editor;
  const { selection, document } = value;
  const paragraph =
    selection &&
    document.getClosest(
      selection.anchor.key,
      // @ts-ignore
      ({ type }) => typeof type === 'string' && (type === 'paragraph' || type.startsWith('heading-')),
    );
  if (!paragraph) {
    return false;
  }

  const list = paragraph.data.list;
  return !!(list && !list.isOrdered && list.isTaskList);
}
